<template>
    <h1>글쓰기</h1>
    <div class="write">
        <input v-model="title">
    <textarea v-model="content"></textarea>
    <br>
    <button @click="write">글쓰기</button>
    </div>
</template>

<script>
export default{
    data(){
        return{
            title : null,
            content : null,
        }
    },
    methods : { 
        write(){
            let saveData = {};
            saveData.title = this.title;
            saveData.content = this.content;
            //alert(saveData.title);
            this.$axios.post(this.$server+'/write', JSON.stringify(saveData), {
                headers: {"Content-Type" : "application/json"}
            })
            .then((res)=>{
                if(res.data.result == 1){
                    alert("데이터베이스에 저장을 완료했습니다.");
                    //location.href='/boardList';
                    this.$router.push("boardList");
                }else{
                    alert(" 문제가 발생했습니다.");
                }
            })
            .catch((err) => {
                alert("문제가 발생했습니다"+err);
            });
        }
    }

}
</script>

<style scoped>
    .write{
        margin: 0 auto;
        padding: 5px;
        width: 800px;
        height: 500px;
        background-color: bisque;
    }
    .write textarea{
        width: 90%;
        height: 80%;
    }
    .write input{
        width: 90%;
    }
</style>