<template>
    <h1>글쓰기</h1>
    <div class="update">
        <input v-model="title">
    <textarea v-model="content"></textarea>
    <br>
    <button @click="update">글쓰기</button>
    </div>
</template>

<script>
export default{
    data(){
        return{
            requsestBody : this.$route.query,
            title : null,
            content : null,
            detail : {
                bno : this.$route.query.bno,
                btitle : '',
                bcontent : '',
            }
        }
    },
    mounted(){
        this.boardDetail();
    },
    methods : { 
       boardDetail() {
      //alert('페이지가 구동되면 바로 호출됩니다.');
      this.$axios
        .get(this.$server + "/detail?bno="+this.$route.query.bno)
        .then((res) => {
          this.detail = res.data.detail;
          this.title=this.detail.btitle;
          this.content = this.detail.bcontent;
        })
        .catch((err) => {
          alert("오류가 발생했습니다." + err);
        });
    },
        update(){
            let saveData = {};
            saveData.title = this.title;
            saveData.content = this.content;
            saveData.bno = this.detail.bno
            //alert(saveData.title);
            this.$axios.patch(this.$server+'/update', JSON.stringify(saveData), {
                headers: {"Content-Type" : "application/json"}
            })
            .then((res)=>{
                if(res.data.result == 1){
                    alert("수정을 완료했습니다.");
                    //location.href='/boardList';
                    this.$router.push("boardList");
                }else{
                    alert(" 문제가 발생했습니다.");
                }
            })
            .catch((err) => {
                alert("문제가 발생했습니다"+err);
            });
        }
    }

}
</script>

<style scoped>
    .update{
        margin: 0 auto;
        padding: 5px;
        width: 800px;
        height: 500px;
        background-color: bisque;
    }
    .update textarea{
        width: 90%;
        height: 80%;
    }
    .update input{
        width: 90%;
    }
</style>